import React from "react";


function logIn(){
    return (
        <div>
                 <h1 className="text-white"> Log In </h1>
            <div className="mt-40 flex flex-col items-center justify-center text-center  ">
               <h1  className="font-bold text-3xl">Log In</h1>
                               
                     <input type="email" placeholder="Enter Your Email "  className="outline-none border border-gray-400 rounded-3xl p-4 m-3" />
                   <input type="text" placeholder="Enter password"   className="outline-none border border-gray-400 rounded-3xl p-4 m-3"/>
                   
                   <button type="submit" className="border border-purple-500 rounded-3xl p-3 m-3 bg-purple-300">Log In </button> 
          
 

                </div>

                   </div>
 
    );
}

export default logIn;